import React from 'react'

const Dashboard = React.lazy(() => import('./views/dashboard/Dashboard'));

// Category
const Category = React.lazy(() => import('./views/category/Category'));
const CategoryCreate = React.lazy(() => import('./views/category/Create'));

// Brand
const Brand = React.lazy(() => import('./views/brand/Brand'));
const BrandCreate = React.lazy(() => import('./views/brand/Create'));
const BrandCategory = React.lazy(() => import('./views/brand/BrandCategory'));

// Product
const Product = React.lazy(() => import('./views/product/Product'));
const ProductCreate = React.lazy(() => import('./views/product/Create'));
const Page404 = React.lazy(() => import('./views/pages/page404/Page404'));

// Media
const Media = React.lazy(() => import('./views/media/Media'));

const Pages = React.lazy(() => import('./views/CMS/pages'));
const AddPages = React.lazy(() => import('./views/CMS/addPages'));
const Menu = React.lazy(() => import('./views/CMS/menu'));
const AddMenu = React.lazy(() => import('./views/CMS/addMenu'));

const EnquiriesList = React.lazy(() => import('./views/enquiries/Enquiries'));

const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/dashboard', name: 'Dashboard', element: Dashboard }, 
  { path: '/category', exact: true,  name: 'Category', element: Category }, 
  { path: '/category/add', name: 'Add Category', element: CategoryCreate }, 
  { path: '/brands', exact: true,  name: 'Brand', element: Brand }, 
  { path: '/brands/add', name: 'Add Brand', element: BrandCreate },
  { path: '/brand-category/', name: 'Brand Category', element: BrandCategory}, 
  { path: '/products', name: 'Product', element: Product }, 
  { path: '/products/add', name: 'Add Product', element: ProductCreate}, 
  { path: '/products', name: 'Product', element: Product }, 

  { path: '/media', name: 'Media', element: Media },
  { path: '/pages', name: 'Pages', element: Pages },
  { path: '/pages/add', name: 'Add Pages', element: AddPages },
  { path: '/menu', name: 'Menu Management', element: Menu },
  { path: '/menu/add', name: 'Add Menu', element: AddMenu },
  { path: '/enquiries/', name: 'Enquiries', element: EnquiriesList },
  
  
  { path: '*', name: 'Brand', element: Page404 },

]

export default routes
